import * as React from 'react';
import { Button } from '@material-ui/core';
import { GlobalFunction } from './GlobalFunction';

interface IProps {
    lastSave?: Date;
    disableCloseTicket?: boolean;
    disableFinalAssessment?: boolean;
    disableAssign?: boolean;
    disableEscalate?: boolean;

    onSaveForNow?: () => void;
    onSubmitTicket?: () => void;
    onResolveTicket?: () => void;
    onSummarizeAssessment?: () => void;
    onGenerateApprovalCode?: () => void;

    onAssign?: () => void;
    onEscalate?: () => void;
    onUpdateTicket?: () => void;
    onFinalAssessment?: () => void;
    onCloseTicket?: () => void;
}

export class TicketFooter extends React.Component<IProps> {

    interval?: NodeJS.Timeout;

    componentDidMount() {
        this.interval = setInterval(() => this.setState({ time: Date.now() }), 5000);
    }

    render(): JSX.Element {
        return (
            <div className='footer'>

                {this.props.onSaveForNow ? (
                    <Button 
                        className='button-transparent' 
                        variant='outlined'
                        onClick={this.props.onSaveForNow}>
                        <div style={{display: 'block'}}>
                            <div className='text'>Save For Now</div>
                            {this.props.lastSave ? (
                                <div className='small-text'>
                                    {`Auto-saved ${GlobalFunction.getTimeDeffirence(this.props.lastSave, new Date())} ago`}
                                </div>
                            ) : null}
                        </div>
                    </Button>
                ) : null}

                {this.props.onResolveTicket ? (
                    <Button 
                        className='button'
                        disabled={this.props.onResolveTicket === null}
                        onClick={this.props.onResolveTicket}>
                        Mark as Resolved
                    </Button>
                ) : null}
                
                {this.props.onSubmitTicket ? (
                    <Button 
                        className='button'
                        disabled={this.props.onSubmitTicket === null}
                        onClick={this.props.onSubmitTicket}>
                        Submit Ticket
                    </Button>
                ) : null}

                {this.props.onAssign ? (
                    <Button 
                        className='button'
                        color='default'
                        disabled={this.props.disableAssign}
                        onClick={this.props.onAssign}>
                        Assign
                    </Button>
                ) : null}

                {this.props.onEscalate ? (
                    <Button 
                        className='button-error'
                        disabled={this.props.disableEscalate}
                        onClick={this.props.onEscalate}>
                        Escalate
                    </Button>
                ) : null}

                {this.props.onUpdateTicket ? (
                    <Button 
                        className='button'
                        disabled={this.props.onUpdateTicket === null}
                        onClick={this.props.onUpdateTicket}>
                        Update Ticket
                    </Button>
                ) : null}

                {this.props.onFinalAssessment ? (
                    <Button 
                        className='button'
                        disabled={this.props.disableFinalAssessment}
                        onClick={this.props.onFinalAssessment}>
                        Proceed with Final Assessment
                    </Button>
                ) : null}

                {this.props.onSummarizeAssessment ? (
                    <Button 
                        className='button'
                        disabled={this.props.onSummarizeAssessment === null}
                        onClick={this.props.onSummarizeAssessment}>
                        Summarize Assessment
                    </Button>
                ) : null}

                {this.props.onGenerateApprovalCode ? (
                    <Button 
                        className='button'
                        disabled={this.props.onGenerateApprovalCode === null}
                        onClick={this.props.onGenerateApprovalCode}>
                        Generate Approval Code
                    </Button>
                ) : null}

                {this.props.onCloseTicket ? (
                    <Button 
                        className='button'
                        disabled={this.props.disableCloseTicket}
                        onClick={this.props.onCloseTicket}>
                        Close Ticket
                    </Button>
                ) : null}
            </div>
        );
    }
}