import * as React from 'react';
import { Grid, Tabs, Tab } from '@material-ui/core';
import { ClientData } from 'Pages/CustomerCarePage/Models';
import { API } from 'Pages/CustomerCarePage/API';
import { Payloads } from 'Pages/CustomerCarePage/Payloads';
import { BasicInformationSection } from './BasicInformationSection';
import { Loading } from 'Pages/CustomerCarePage/Components/Loading';

interface IProps {
    clientId: string;
}

interface IStates {
    client?: ClientData;
    selectedTab: number;
}

export class ClientProfileSection extends React.Component<IProps, IStates> {

    informationTabs = [
        { value: 1, label: 'Basic Information' },
        { value: 2, label: 'Contracts/Branches' },
        { value: 3, label: 'Member Fields' },
        { value: 4, label: 'Servicing Instructions' }
    ];

    accountTabs = [
        { value: 5, label: 'HMO Information' },
        { value: 6, label: 'Members' },
        { value: 7, label: 'History' },
        { value: 8, label: 'Documents' }
    ];

    activitiesTabs = [
        { value: 9, label: 'Audit Logs' }
    ];

    constructor(props: IProps) {
        super(props);

        this.state = {
            selectedTab: 1
        };
    }

    componentDidMount() {
        API.get(`clients/account/hmo-info/${this.props.clientId}`,
            (response) => {
                console.log(response);
                this.setState({client: Payloads.getClientData(response.client)});
            },
            (error) => {
                console.log(error);
            });
    }

    render() : JSX.Element {
        return (
            <div className='section' style={{width: '100%'}}>
                <div style={{paddingTop: '2.5rem'}} />
                <Grid container className='sub-section' spacing={5}>
                    {this.renderSideBar()}
                    {this.renderMainContent()}

                    {/* {this.state.benefitListUpdated ? (
                        this.renderBenefitsList()
                    ) : (
                        <Grid item xs={10}>
                            <Loading height='10rem'/>
                        </Grid>
                    )} */}
                    
                </Grid>
            </div>
        );
    }

    renderSideBar(): JSX.Element {
        const rightBorderStyle = {
            borderRight: '#3C394A22 solid 1px'
        }
        return (
            <Grid item xs={2} style={rightBorderStyle}>
                {/* TEST */}
                {this.renderSideTabs(this.informationTabs)}
                {this.renderSideTabs(this.accountTabs)}
                {this.renderSideTabs(this.activitiesTabs)}
            </Grid>
        );
    }

    renderSideTabs(tabs: any[]): JSX.Element {
        return (
            <Tabs
                className='custom-vertical-tabs' 
                orientation='vertical'
                value={this.state.selectedTab}
                variant='scrollable'
                scrollButtons='auto'
                indicatorColor='primary'
                onChange={(_event: React.ChangeEvent<{}>, value: number) => {
                    this.setState({selectedTab: value});
                }}>
                {tabs.map(tab => {
                    return (
                        <Tab 
                            value={tab.value} 
                            label={
                                <div style={{width: '100%'}}>
                                    <div className='header-3'>{tab.label}</div>
                                </div>
                            } />
                    )
                })}
            </Tabs>
        );
    }

    renderMainContent(): JSX.Element {
        return (
            <Grid item xs={10}>
                {this.state.client ? (
                    <BasicInformationSection client={this.state.client} />
                ) : (
                    <Loading height='10rem'/>
                )}
                
            </Grid>
        );
    }
}