



export class GlobalFunction {

    static toNameFormat(fname: string, mname: string, lname: string) {
        return `${fname} ${GlobalFunction.getIntials(mname)} ${lname}`;
    }

    static formatNumber(num: number, isDecimal: boolean = false) {
        let value = '';
        if (isDecimal) {
            value = num.toFixed(2);
        } else {
            value = num.toString();
        }
        return value.replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,');
    }


    static getIntials(text: string) {
        var initials = '';
        var wordArray = text.split(' ');
        for (var i = 0; i < wordArray.length; i++) {
            initials += wordArray[i].substring(0,1).toUpperCase() + '.';
        }
        return initials;
    }

    static toCommonDateString(paramDate: Date | string) {
        let date = typeof paramDate === 'string' ? new Date(paramDate) : paramDate;
        if (date) {
            const monthNames = [
                "January", "February", "March",
                "April", "May", "June", "July",
                "August", "September", "October",
                "November", "December"
            ];
            const day = date.getDate();
            const monthIndex = date.getMonth();
            const year = date.getFullYear();
            return (monthNames[monthIndex] + " " + day + ", " + year);
        }

        return '';
    }

    static toShortDateString(paramDate: Date | string) {
        let date = typeof paramDate === 'string' ? new Date(paramDate) : paramDate;
        if (date) {
            const monthNames = [
                "Jan", "Feb", "Mar", "Apr", "May", "Jun", 
                "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"
            ];
            const day = date.getDate();
            const monthIndex = date.getMonth();
            const year = date.getFullYear();
            return (monthNames[monthIndex] + " " + day + ", " + year);
        }

        return '';
    }

    static toDateAndTime(paramDate: Date | string) {
        let date = typeof paramDate === 'string' ? new Date(paramDate) : paramDate;
        if (date) {
            const monthNames = [
                "January", "February", "March",
                "April", "May", "June", "July",
                "August", "September", "October",
                "November", "December"
            ];
            const day = date.getDate();
            const monthIndex = date.getMonth();
            const year = date.getFullYear();
            const hours = date.getHours() - (date.getHours() > 12 ? 12 : 0);
            const minutes = date.getMinutes();
            const seconds = date.getSeconds();
            const meridian = (date.getHours() > 12 ? "PM" : "AM");
            
            var newSec = '0' + seconds;
            if (seconds.toLocaleString().length !== 1) newSec = seconds.toLocaleString()

            return (
                monthNames[monthIndex] + " " + 
                day + ", " + 
                year + " - " + 
                hours + ":" + 
                minutes + ":" + 
                newSec + " " + 
                meridian);
        }

        return '';
    }

    static getTimeDeffirence(fromDate: Date, toDate: Date) {
        const seconds = (toDate.getTime() - fromDate.getTime()) / 1000;
        const scaleList = [ 
            ['day', 'days'], 
            ['hour', 'hours'], 
            ['minute', 'minutes'], 
            ['second', 'seconds'] 
        ];
        const valueList = [
            Math.round(seconds / 86400),
            Math.round(seconds / 3600) % 24,
            Math.round(seconds / 60) % 60,
            Math.round(seconds) % 60
        ];
        
        let index = 0;
        for (let i = 0; i < valueList.length; i++) {
            if (valueList[i] > 0) {
                index = i;
                break;
            }
        }

        if (index) {
            return `${valueList[index]} ${scaleList[index][valueList[index] == 1 ? 0 : 1]}`
        }
        return '';
    }

    static getAge(birthDate: any) {
        if (birthDate) {
            var ageInMillis = Date.now() - birthDate;
            return Math.abs(new Date(ageInMillis).getUTCFullYear() - 1970);
        }

        return null;
    }

    static generateTicketNumber(ticketType: string, specificType: string, appNo: string) {
        const ticketTypes = {
            "Availment": "AVL",
            "Inquiry": "INQ"
        };
        const specificTypes = {
            "Inpatient": "INP",
            "Outpatient Procedure": "OP",
            "Emergency Room": "ER",
            "Maternity": "MAT",
            "Consultation": "CON"
        }
        const tempString = (ticketType === 'Inquiry' ? '' : `${specificTypes[specificType]}-`);
        return `${ticketTypes[ticketType]}-${tempString}${appNo}`;
    }
}