
import * as React from 'react';
import Grid from '@material-ui/core/Grid';

import './../style.scss';
import { LeftPanelSection } from '../LeftPanelSection/LeftPanelSection';
import { API } from './../API';
import { ConfinementSection } from './Components/ConfinementSection';
import { AvailmentSection } from './Components/AvailmentSection';
import { TicketInfoSection } from './Components/TicketInfoSection';
import { TicketData, MemberData, DropdownChoice, ConfinementDetails, InquiryData, AvailmentDetails, ConsultationDetails, ClientData } from '../Models';
import { InquirySection } from './Components/InquirySection';
import { Payloads } from '../Payloads';
import { ConsultationSection } from './Components/ConsultationSection';
import { Processmaker } from '../Processmaker';
import { EmergencyRoomSection } from './Components/EmergencyRoomSection';
import { TicketFooter } from '../TicketFooter';
import { Loading } from '../Components/Loading';
import { Snackbar } from '@material-ui/core';
import { GlobalFunction } from '../GlobalFunction';
import { CustomTabs } from '../Components/CustomTab';
import { ClientProfileSection } from './Components/ClientProfileSection/ClientProfileSection';
import { PartnerNetworkSection } from '../ResolveTicketPage/Components/PartnerNetworkSection';
import { CheckUtilizationSection } from '../ResolveTicketPage/Components/CheckUtilizationSection';

interface IProps {
    history: any;
}

interface IStates {
    tabSelected: number;
    createTicketAttemp: boolean;
    isIcd10CodesLoaded: boolean;
    isSaving: boolean;
    loadedResources: number;
    snackbarMessage: string | null;
    lastSave?: Date;

    /* Can save to tmp */
    client?: ClientData;
    member?: MemberData;
    ticket: TicketData;
    availment: AvailmentDetails;
    confinement: ConfinementDetails;
    consultation: ConsultationDetails;
    inquiries: InquiryData[];
}

export class CreateTicketPage extends React.Component<IProps, IStates> {

    static RESOURCES_TO_LOAD = 1;

    tabList = [
        { value: 1, label: 'Ticket Details' }, 
        { value: 2, label: 'Client Profile' }, 
        { value: 3, label: 'Partner Network' }, 
        { value: 4, label: 'Exclusion' }, 
        { value: 5, label: 'Utilization' }, 
        { value: 6, label: 'Tickets' },  
        { value: 7, label: 'Contract' }
    ];

    icd10Codes: DropdownChoice[];
    cptCodes: DropdownChoice[];
    facilities: DropdownChoice[];
    doctors: DropdownChoice[];
    agents: DropdownChoice[];
    
    constructor(props: IProps) {
        super(props);

        this.state = {
            tabSelected: 1,
            createTicketAttemp: false,
            isIcd10CodesLoaded: false,
            isSaving: false,
            snackbarMessage: null,
            loadedResources: 0,

            ticket: {
                ticketChannel: 'Call',
                transactionNo: '9999-9999',
                ticketSource: '',
                memberName: '',
                clientName: '',
                brokerName: '',
                facility: { value: '', label: '' },
                department: '',
                callerName: '',
                contactNo: '',
                ticketType: '',
                specificType: '',
                urgency: 'High',
                agent: { id: '', name: '' },
                additionalNotes: '',
                additionalFiles: [],
                ticketNo: '',
                pmCaseId: '',
                status: 'Open',
                createdBy: { 
                    id: localStorage['user_id'], 
                    name: `${localStorage['first_name']} ${localStorage['last_name']}` 
                },
                dateCreated: new Date(),
                dateUpdated: new Date(),
                dateEscalated: null
            },
            availment: {
                provider: { value: '', label: '' },
                diagnoses: []
            },
            confinement: {
                provider: { value: '', label: '' },
                dateAdmitted: new Date(),
                confinementKind: '',
                roomType: '',
                costPerDay: 0,
                doctor: { value: '', label: '' },
                initialDiagnosis: { value: '', label: '' },
                amount: 0
            },
            consultation: {
                provider: { value: '', label: '' },
                diagnosis: { value: '', label: '' },
                doctor: { value: '', label: '' },
                notes: '',
                assessmentRemarks: '',
                assessmentType: 'Pending',
                approvedAmount: 0
            },
            inquiries: []
        };

        this.facilities = [];
        this.agents = [];
        this.doctors = [];
        this.cptCodes = [];
        this.icd10Codes = [];
    }

    componentDidMount() {
        API.get('facilities',
            (response) => {
                this.facilities = response.map(facility => {
                    return { 
                        value: facility.facility_id,
                        label: facility.name 
                    }
                });
            },
            (error) => {
                console.log(error);
            });



        const tempIcd10Codes = localStorage.getItem('icd10_codes');
        if (!tempIcd10Codes) {
            API.get('icd10-codes?filter[limit]=1000',
                (response) => {
                    this.icd10Codes = response.map(icd10Code => {
                        return { 
                            value: icd10Code.full_code,
                            label: icd10Code.abbreviated_description 
                        };
                    });
                    localStorage.setItem('icd10_codes', JSON.stringify(this.icd10Codes));
                    this.setState({isIcd10CodesLoaded: true});
                },
                (error) => {
                    console.log(error);
                });
        } else {
            this.icd10Codes = JSON.parse(tempIcd10Codes);
            console.log("Using the local copy of ICD10 Codes");
        }
        


        const tempCptCodes = localStorage.getItem('cpt_codes');
        if (!tempCptCodes) {
            API.get('cpts',
                (response) => {
                    this.cptCodes = response.map(cptCode => {
                        return {
                            value: cptCode.code.toString(),
                            label: cptCode.label 
                        };
                    });
                    localStorage.setItem('cpt_codes', JSON.stringify(this.cptCodes));
                }, 
                (error) => {
                    console.log(error);
                });
        } else {
            this.cptCodes = JSON.parse(tempCptCodes);
            console.log("Using the local copy of CPT Codes");
        }
        


        API.get('doctors',
            (response) => {
                this.doctors = response.map(doctor => {
                    return {
                        value: doctor._id,
                        label: ('Dr. ' + doctor.first_name +  ' ' + doctor.last_name),
                        params: [doctor.type]
                    };
                });
            }, 
            (error) => {
                console.log(error);
            });

        API.get(`tmp?filter[where][and][0][user_id]=${localStorage['user_id']}&filter[where][and][1][module]=Customer%20Care`,
            (response) => {
                console.debug(response);
                response = response.filter(item => item.process === 'Create Ticket');
                if (response.length > 0) {
                    this.setState({
                        lastSave: new Date(response[0].timestamp),
                        member: response[0].data.member,
                        ticket: response[0].data.ticket,
                        availment: response[0].data.availment,
                        confinement: response[0].data.confinement,
                        consultation: response[0].data.confinement,
                        inquiries: response[0].data.inquiries
                    });
                }
                this.setState({loadedResources: this.state.loadedResources + 1});
            },
            (error) => {
                console.log(error);
                this.setState({loadedResources: this.state.loadedResources + 1});
            });
    }

    uploadFiles = (nextAction: Function) => {
        let hasFiles = false;

        const files = new FormData();
        this.state.ticket.additionalFiles.forEach(file => {
            if (!file['field']) {
                files.append(`supported_documents`, file);
                hasFiles = true;
            }
        });

        this.setState({isSaving: true});

        if (hasFiles) {
            API.postFormData('tmp/upload?path=customer-care', files,
                (uploadResponse) => {
                    nextAction(uploadResponse);
                },
                (error) => {
                    console.log(error);
                    this.setState({
                        isSaving: false,
                        snackbarMessage: 'Failed to upload files.'
                    });
                });
        } else {
            nextAction([]);
        }
    }

    submitTicket = () => {

        this.setState({createTicketAttemp: true});
        const ticket = this.state.ticket;
        const confinement = this.state.confinement;
        const availment = this.state.availment;
        const consultation = this.state.consultation;
        const inquiries = this.state.inquiries;
        
        if (!this.state.member && !this.state.client) return;

        /* Confinement Details must have all contents */
        if (ticket.ticketType === 'Availment' && ticket.specificType === 'Inpatient') {
            if (confinement.provider.value === '' ||
                confinement.confinementKind === '' ||
                confinement.costPerDay === 0 ||
                confinement.doctor.value === '' ||
                confinement.initialDiagnosis.value === '' ||
                confinement.roomType === '') {
                return;
            }
        }

        this.setState({isSaving: true});

        this.uploadFiles((uploadResponse) => {
            const payload = Payloads.getPostTicketPayload(
                ticket, 
                
                !uploadResponse.error && uploadResponse.length > 0 ? (
                    this.state.ticket.additionalFiles
                        .filter(file => file['field'])
                        .concat(uploadResponse)
                ) : (
                    this.state.ticket.additionalFiles
                ),
                this.state.member,
                this.state.client,
                confinement, 
                availment, 
                inquiries, 
                consultation);
            API.post('customer-care/tickets', JSON.stringify(payload),
                (postTicketResponse) => { 
                    console.log(postTicketResponse);
                    if (postTicketResponse['_id']) {
                        console.log('Ticket No: '+postTicketResponse['id']);
                        this.submitTicketProcessMaker(postTicketResponse['_id'], this.state.ticket);
                    } else {
                        this.setState({
                            isSaving: false,
                            snackbarMessage: 'Failed to create ticket.'
                        });
                    }  
                },
                (error) => {
                    console.log(error);
                    this.setState({
                        isSaving: false,
                        snackbarMessage: 'Failed to create ticket.'
                    });
                });
        });
    }

    submitTicketProcessMaker = (ticketId: string, ticket: TicketData) => {
        let is_availment = false;
        if (ticket.ticketType === 'Availment') {
            is_availment=true;
        }
        let trigger_id = "3905447335db2a32daafd33048658514"; //should refer to redux createtickettrigger_id!!!
        let body = {
            "pro_uid" : "1538417025dae8dae9ff7a9016454302", //should refer to redux createticketproject_id!!!
            "tas_uid":"6092309955dae8db0a85907021073537",   //should refer to redux createticketstarttask_id!!!
            "ticket_no": ticketId,
            "can_be_resolved_imm": 0,
            "is_availment":is_availment
        };
        console.log('Body:', body);

        Processmaker.post('cases', body,
            (casesResponse) => {
                if (casesResponse['app_uid']) {
                    processmakerExecuteTrigger(
                        casesResponse['app_uid'], 
                        casesResponse['app_number'],
                        trigger_id
                    );
                }
            }, 
            (error) => {
                console.log(error);
                this.setState({
                    isSaving: false,
                    snackbarMessage: 'Failed to create ticket.'
                });
            }
        );
        
        const processmakerExecuteTrigger = (appUID: string, appNo: string, triggerId: string) => {
            Processmaker.put(`cases/${appUID}/execute-trigger/${triggerId}`, body,
                (triggerResponse) => {
                    console.log('Trigger Response: ', triggerResponse);
                    processmakerVariable(appUID, appNo);
                }, 
                (error) => {
                    console.log(error);
                    this.setState({
                        isSaving: false,
                        snackbarMessage: 'Failed to create ticket.'
                    });
                });
        }

        const processmakerVariable = (appUID: string, appNo: string) => {
            Processmaker.put(`cases/${appUID}/variable`, body,
                (variableResponse) => {
                    console.log('Set Variable Response: ', variableResponse);
                    patchCaseUID(appUID, appNo);
                }, 
                (error) => {
                    console.log(error);
                    this.setState({
                        isSaving: false,
                        snackbarMessage: 'Failed to create ticket.'
                    });
                });
        }

        const patchCaseUID = (appUID: string, appNo: string) => {
            API.patch(`customer-care/tickets/${ticketId}`, { pmaker_case_uid: appUID },
                (patchTicketResponse) => {
                    console.log('Patch Response: ', patchTicketResponse);
                    redirectToNextPage(appUID, appNo);
                },
                (error) => {
                    console.log(error);
                    this.setState({
                        isSaving: false,
                        snackbarMessage: 'Failed to create ticket.'
                    });
                });
        }

        const redirectToNextPage = (appUID: string, appNo: string) => {
            const ticketNo = GlobalFunction.generateTicketNumber(ticket.ticketType, ticket.specificType, appNo);
            const url = 
                ticket.ticketType === 'Inquiry' || ticket.specificType === 'Inpatient' ? (
                    `#/customer-care/update-ticket/${appUID}/${ticketNo}/${ticketId}`
                ) : (
                    `#/customer-care/resolve-ticket/${appUID}/${ticketNo}/${ticketId}`
                );
            this.props.history.push(url);
            window.location.reload();
            this.setState({isSaving: false});
        }
    }

    saveForNow = () => {
        this.uploadFiles((uploadResponse) => {
            console.log(uploadResponse);
            const payload = {
                user_id: localStorage['user_id'],
                module: 'Customer Care',
                process: 'Create Ticket',
                timestamp: new Date(),
                data: {
                    member: this.state.member,
                    ticket: {
                        ...this.state.ticket,
                        additionalFiles: !uploadResponse.error && uploadResponse.length > 0 ? (
                            this.state.ticket.additionalFiles
                                .filter(file => file['field'])
                                .concat(uploadResponse)
                        ) : (
                            this.state.ticket.additionalFiles
                        )
                    },
                    availment: this.state.availment,
                    confinement: this.state.confinement,
                    consultation: this.state.confinement,
                    inquiries: this.state.inquiries
                }
            };
            console.log('Saving this: ', payload);
            API.post('tmp', JSON.stringify(payload),
                (response) => {
                    console.log(response);
                    if (response._id) {
                        this.setState({
                            isSaving: false,
                            snackbarMessage: 'Progress saved.',
                            lastSave: new Date()
                        });
                    } else {
                        this.setState({
                            isSaving: false,
                            snackbarMessage: 'Failed to save progress.'
                        });
                    }
                },
                (error) => {
                    console.log(error);
                    this.setState({
                        isSaving: false,
                        snackbarMessage: 'Failed to save progress.'
                    });
                });
        });
    }

    render(): JSX.Element {

        return (
            <>
            <Grid container className='customer-care-main'>

                {this.state.loadedResources !== CreateTicketPage.RESOURCES_TO_LOAD || this.state.isSaving ? (
                    <div 
                        style={{
                            backgroundColor: '#00000055', 
                            zIndex: 100, 
                            position: 'fixed', 
                            width: '100%', 
                            height: '100%'}}>
                        <Loading height='15rem' size={100}/>
                    </div>
                    
                ) : (null)}

                <Snackbar
			        anchorOrigin={{vertical: 'top',horizontal: 'right'}}
			        open={this.state.snackbarMessage ? true : false}
			        autoHideDuration={6000}
			        onClose={() => {
                        this.setState({snackbarMessage: null});
                    }}
                    message={this.state.snackbarMessage} />

                <LeftPanelSection 
                    client={this.state.client}
                    member={this.state.member}
                    ticketStatus='Create'
                    promptChooseMember={this.state.createTicketAttemp}
                    onUpdateMemberStateChange={(member: MemberData) => {
                        if (member) {
                            const hasActiveContract = (
                                member.contract && 
                                member.contract.status === 'Active'
                            );
                            if (!hasActiveContract && this.state.ticket.ticketType === 'Availment') {
                                this.state.ticket.ticketType = '';
                                this.setState({ticket: Object.assign({}, this.state.ticket)});
                            }
                        }

                        this.setState({member: member});
                    }}
                    onUpdateClientStateChange={(client: ClientData) => {
                        this.setState({client: client});
                    }} />

                {this.state.client ? (
                    <>
                    <CustomTabs 
                        tabList={this.tabList}
                        selectedTab={this.state.tabSelected}
                        setSelectedTab={(index: number) => {
                            this.setState({tabSelected: Math.min(index, 2)});
                        }} />
                    <div className={`main-content${this.state.tabSelected === 1 ? '-flex' : ''}`}>
                        {this.state.tabSelected === 1 ? (
                            this.renderMainContent()
                        ) : this.state.tabSelected === 2 ? (
                            <ClientProfileSection clientId={this.state.client.id} />
                        ) : this.state.tabSelected === 3 ? (
                            <PartnerNetworkSection clientId={this.state.client.id}/>
                        ) : this.state.tabSelected === 4 ? (
                            <div className='header'>TAB 3</div>
                        ) : this.state.tabSelected === 5 ? (
                            <CheckUtilizationSection />
                        ) : this.state.tabSelected === 6 ? (
                            <div className='header'>TAB 5</div>
                        ) : this.state.tabSelected === 7 ? (
                            <div className='header'>TAB 6</div>
                        ) : null}
                    </div>
                    </>
                ) : (
                    <div className='main-content-flex'>
                        {this.renderMainContent()}
                    </div>
                )}  
            </Grid>

            <TicketFooter 
                lastSave={this.state.lastSave}
                onSaveForNow={() => {
                    this.saveForNow();
                }} 
                onSubmitTicket={() => {
                    this.submitTicket();
                }} />
            </>
        );
    }

    renderMainContent(): JSX.Element {
        return (
            <>
            <TicketInfoSection 
                member={this.state.member}
                client={this.state.client}
                ticket={this.state.ticket} 
                facilities={this.facilities}
                agents={this.agents}
                createTicketAttemp={this.state.createTicketAttemp}
                refreshTicketState={() => {
                    this.setState({ticket: Object.assign({}, this.state.ticket)});
                }} />

            {this.state.ticket.ticketType === 'Availment' ? (
            
                this.state.ticket.specificType === 'Outpatient Procedure' ? (  
                    <AvailmentSection
                        availment={this.state.availment}
                        facilities={this.facilities}
                        doctors={this.doctors}
                        icd10Codes={this.icd10Codes}
                        cptCodes={this.cptCodes}
                        createTicketAttemp={this.state.createTicketAttemp}
                        refreshAvailmentState={() => {
                            this.setState({availment: Object.assign({}, this.state.availment)});
                        }} />

                ) : this.state.ticket.specificType === 'Inpatient' ? ( 
                    <ConfinementSection
                        confinement={this.state.confinement}
                        facilities={this.facilities}
                        doctors={this.doctors}
                        cptCodes={this.icd10Codes}
                        createTicketAttemp={this.state.createTicketAttemp}
                        refreshConfinementState={() => {
                            this.setState({confinement: this.state.confinement});
                        }} />
                ) : this.state.ticket.specificType === 'Emergency Room' ? (
                    <EmergencyRoomSection
                        availment={this.state.availment}
                        facilities={this.facilities}
                        doctors={this.doctors}
                        icd10Codes={this.icd10Codes}
                        cptCodes={this.cptCodes}
                        createTicketAttemp={this.state.createTicketAttemp}
                        refreshAvailmentState={() => {
                            this.setState({availment: this.state.availment});
                        }} />
                    
                ) : this.state.ticket.specificType === 'Consultation' ? ( 
                    <ConsultationSection
                        consultation={this.state.consultation}
                        facilities={this.facilities}
                        doctors={this.doctors}
                        icd10Codes={this.icd10Codes}
                        createTicketAttemp={this.state.createTicketAttemp}
                        refreshConsultationState={() => {
                            this.setState({consultation: this.state.consultation});
                        }} />
                ) : null

            ) : this.state.ticket.ticketType === 'Inquiry' ? (
                <InquirySection
                    inquiries={this.state.inquiries}
                    refreshInquiryState={() => {
                        this.setState({inquiries: this.state.inquiries});
                    }} />
            ) : null}
            </>
        );
    }
}